<template>
  <div class="hello">
    {{ msg }} | {{ message }} | {{ news }}
<!--    <sui-button third>aaaaa</sui-button>-->
    <sui-button primary>Primary</sui-button>
    <sui-button secondary>Secondary</sui-button>
    <sui-grid :columns="2">
      <sui-grid-row>
        <sui-grid-column centered>
          <h3 is="sui-header">销售情况</h3>
        </sui-grid-column>
      </sui-grid-row>
      <sui-grid-row>
        <sui-grid-column>
          <a style="position: relative">
            <div slot="label" tabindex="0">
              <sui-button content="销量" size="tiny" slot="label" compact></sui-button>
              <sui-label circular color="red" size="tiny" floating>吨</sui-label>
              <a is="sui-label" slot="label" basic>2,048</a>
            </div>
          </a>

        </sui-grid-column>
        <sui-grid-column>bbb</sui-grid-column>
      </sui-grid-row>
    </sui-grid>

  </div>

</template>

<script>
  export default {
    name: 'HelloWorld',
    data() {
      return {
        msg: 'Welcome to Your Vue.js App',
        message: 'learn vue',
        news: 'this is my own place'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



